import React,{useState,useRef} from 'react'
import formimg from './../images/ill.png'
import Swal from 'sweetalert2'
import {useAuthState} from "react-firebase-hooks/auth"
import { auth } from "../firebase-config";
import { GoogleAuthProvider , signInWithPopup,signInWithRedirect, socialMediaAuth,GithubAuthProvider } from "firebase/auth";
import {db} from "../firebase-config"
import { collection , getDocs ,addDoc,updateDoc,doc} from "firebase/firestore"
import { query, where } from "firebase/firestore";  
import { async } from '@firebase/util';

export default function Course() {
    const [name, setname] = useState("");
    const [phone, setphone] = useState("");
    const [phone2, setphone2] = useState("");
    const [center, setcenter] = useState("");
    const [where, setwhere] = useState("center");
    const nameinput = useRef(null);
    const phoneinput = useRef(null);
    const phone2input = useRef(null);
    const centerinput = useRef(null);
    const whereinput = useRef(null);
    const [userdata] = useAuthState(auth);
    const submitclick =async () =>{
        if(userdata){
            if(name.trim()!==""&&phone.trim()!==""&&phone2.trim()!==""&&center.trim()!=="")
            {
                if(!localStorage.getItem("form")){
                    const userCollectionRef = collection(db,"3ssubmits");
                    await addDoc(userCollectionRef,{
                        name:name,
                        phone:phone,
                        GuardianPhone:phone2,
                        center:center,
                        where:where,
                        googleUserName:userdata.displayName,
                        email:userdata.email,
                        googlePhone:userdata.phoneNumber
                    })
                    nameinput.current.value="";
                    phoneinput.current.value="";
                    phone2input.current.value="";
                    centerinput.current.value="";
                    whereinput.current.value="center";
                    setname("");
                    setphone("");
                    setphone2("");
                    setcenter("");
                    setwhere("");
                    localStorage.setItem("form",true);
                    Swal.fire({
                        icon: 'success',
                        title: 'تم ارسال بياناتك بنجاح'
                    });
                }
                else{
                   Swal.fire({
                       icon: 'error',
                       title: 'يمكنك التسجيل مرة واحدة فقط'
                   });
                }
            }
            else{
                console.log(name);
                console.log(phone);
                console.log(phone2);
                console.log(center);
                Swal.fire({
                    icon: 'error',
                    title: 'يرجي ملئ الحقول'
                });
            }
        }
        else
        {
            Swal.fire({
                icon: 'error',
                title: 'يلزم تسجيل الدخول للأرسال'
            });
        }
    }
  return (
    <div className="tra" id="traqme">
        <div className="container">
            <div className="heading">
                <h2>التسجيل للكورس</h2>
            </div>
            <div className="theform" >
                <div className="inputs">
                    <div>
                        <label>
                        الأسم:
                        <br/>
                        <input type="text" onChange={(e)=>setname(e.target.value)} ref={nameinput}/>
                        </label>
                    </div>
                    <div>
                        <label>
                        رقم الموبايل:
                        <br/>
                        <input type="tel" onChange={(e)=>setphone(e.target.value)} ref={phoneinput}/>
                        </label>
                    </div>
                    <div>
                        <label>
                        رقم ولي الأمر:
                        <br/>
                        <input type="tel" onChange={(e)=>setphone2(e.target.value)} ref={phone2input}/>
                        </label>
                    </div>
                    <div>
                        <label>
                        اسم السنتر:
                        <br/>
                        <input type="text" onChange={(e)=>setcenter(e.target.value)} ref={centerinput}/>
                        </label>
                    </div>
                    <div style={{"display":'flex',
                                "justifyContent":"space-between",
                                "alignItems":"flex-end"
                                }}>
                        <label>
                        الحضور:
                        <br/>
                        <select onChange={(e)=>setwhere(e.target.value)} ref={whereinput}>
                            <option selected value="center">سنتر</option>
                            <option value="online">اون لاين</option>
                        </select>
                        </label>
                        <button onClick={submitclick}>ارسال</button>
                    </div>
                </div>
                <div className="ills">
                    <img src={formimg} alt=""/>
                </div>
            </div>
        </div>
    </div>
  )
}
