import React from 'react'
import s1img from "./../images/1ث.png"
import s2img from "./../images/2ث.png"
import s3img from "./../images/3ث.png"
import { Link } from "react-router-dom";


export default function Edu() {
  return (
    <div className="edu" id="second">
        <div className="container">
            <div className="heading">
                <h2>المراحل الدراسية</h2>
            </div>
            <div className="cont">
                
                <div className="box">
                    <img src={s1img} alt=""/>
                    <div className="content">
                        <h2>الصف الأول الثانوي</h2>
                        <p>شرح منهج الصف الأول الثانوي في الرياضيات مع التراكمي و فهم جزئياته مع التطبيق
                        </p>
                        <div className="down">
                            <div className="ngom">
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                            </div>
                            <Link to={`${process.env.PUBLIC_URL}/soon`}>أبدأ</Link>
                        </div>
                    </div>
                </div>
                
                <div className="box">
                    <img src={s2img} alt=""/>
                    <div className="content">
                        <h2>الصف الثاني الثانوي</h2>
                        <p>شرح منهج الصف الثاني الثاني في الرياضيات مع التراكمي و فهم جزئياته مع التطبيق
                        </p>
                        <div className="down">
                            <div className="ngom">
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                            </div>
                            <Link to={`${process.env.PUBLIC_URL}/soon`}>أبدأ</Link>
                        </div>
                    </div>
                </div>
                
                <div className="box">
                    <img src={s3img} alt=""/>
                    <div className="content">
                        <h2>الصف الثالث الثانوي</h2>
                        <p>شرح منهج الصف الثالث الثانوي في الرياضيات مع التراكمي و فهم جزئياته مع التطبيق
                        </p>
                        <div className="down">
                            <div className="ngom">
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                            </div>
                            <Link to={`${process.env.PUBLIC_URL}/soon`}>أبدأ</Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}
