import React,{useRef} from 'react'
import logo from "./../images/logo.png"
import { Link } from "react-router-dom";
import { GoogleAuthProvider , signInWithPopup,signInWithRedirect, socialMediaAuth,GithubAuthProvider } from "firebase/auth";
import { auth } from "../firebase-config";
import {db} from "../firebase-config"
import { collection , getDocs ,addDoc,updateDoc,doc} from "firebase/firestore"
import { query, where } from "firebase/firestore";  
import { signOut } from "firebase/auth"
import Swal from 'sweetalert2'
import {useAuthState} from "react-firebase-hooks/auth"


export default function Header(props) {

    const burger = useRef(null)
    const nav = useRef(null)
    const line1 = useRef(null)
    const line2 = useRef(null)
    const line3 = useRef(null)
    const burgerclick=()=>{
        nav.current.classList.toggle("nav-active");
        line1.current.classList.toggle("activeline1");
        line2.current.classList.toggle("activeline2");
        line3.current.classList.toggle("activeline3");
    }

    const [userdata] = useAuthState(auth);

    const login=async()=>{
        //google
        const provider = new GoogleAuthProvider();
        await signInWithPopup(auth, provider).catch(res=>{
            if(res=="FirebaseError: Firebase: Error (auth/account-exists-with-different-credential)."){
                Swal.fire({
                    icon: 'error',
                    title: 'تم تسجيل الدخول مسبقاً بهذا الأيميل عن طريق جيت هب',
                    showConfirmButton: false,
                    timer: 2000
                })
            }
        });
        //firebase check
        let user;
        const userCollectionRef = collection(db,"users");
        const queryuser = query(userCollectionRef,where("uid","==",auth.currentUser.uid));
        const data = await getDocs(queryuser);
        user =(data.docs.map((doc)=>({...doc.data(),id:doc.id})))
        if(user.length === 0)
        {
            await addDoc(userCollectionRef,{email:auth.currentUser.email,
                                            name:auth.currentUser.displayName,
                                            uid:auth.currentUser.uid,
                                            photo:auth.currentUser.photoURL,
                                            exp:0,
                                            level:0,
                                            csharp:{0:false},
                                            csharpq:{0:0},
                                            csharpxp:{0:0},
                                            unity:{0:false},
                                            unityq:{0:0},
                                            unityxp:{0:0},
                                            codetime:0,
                                            notifications:[],
                                            favpost:[],
                                            mypostes:[],
                                            badges:[false,false,false,false,false,false,false,false]
                                        });
        }
        localStorage.setItem("codetime",user[0].codetime);
        localStorage.setItem("loged",true);

        Swal.fire({
            icon: 'success',
            title: 'تم تسجيل الدخول بنجاح',
            text:`مرحبا بك ${auth.currentUser.displayName}`
        })
    }

    const logoutdo=async()=>{
        Swal.fire({
            title: 'هل تريد تسجيل الخروج؟',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'نعم ',
            cancelButtonText:"لا"
        }).then((result) => {
            if (result.isConfirmed) {
                localStorage.setItem("codetime",0);
                localStorage.setItem("loged",false);
                signOut(auth);
                Swal.fire({
                    icon: 'success',
                    title: 'تم تسجيل الخروج بنجاح'
                })
            }
        })
    }

  return (
    <header className={props.section?"sec":""}>
        <div className="container">
            <Link to={`${process.env.PUBLIC_URL}/`}>
            <img src={logo} alt="" className="logo"/>
            </Link>
            <nav>
            <ul id="nav" className="test" ref={nav}>
                <li className="link"><Link to={`${process.env.PUBLIC_URL}/`}>الصفحة الرئيسية</Link></li>
                <li className="link">
                    <a href={`${process.env.PUBLIC_URL}/#second`}>المراحل الدراسية</a>
                    <ol className="dropdown">
                        <li className="drop"><Link to={`${process.env.PUBLIC_URL}/soon`}>الأول الثانوي</Link></li>
                        <li className="drop"><Link to={`${process.env.PUBLIC_URL}/soon`}>الثاني الثانوي</Link></li>
                        <li className="drop"><Link to={`${process.env.PUBLIC_URL}/soon`}>الثالث الثانوي</Link></li>
                    </ol>
                </li>
                <li className="link"><a href={`${process.env.PUBLIC_URL}/#traqme`}>التسجيل للكورس</a></li>
                {userdata?<li className="link log" onClick={logoutdo}><Link to={`${process.env.PUBLIC_URL}/`}>تسجيل الخروج</Link></li>:<li className="link log" onClick={login}><Link to={`${process.env.PUBLIC_URL}/`}>تسجيل الدخول</Link></li>}
            </ul>
            </nav>

            <div className="barburger" ref={burger} onClick={burgerclick}>
                <div className="slice" id="line1" ref={line1}></div>
                <div className="slice" id="line2" ref={line2}></div>
                <div className="slice" id="line3" ref={line3}></div>
            </div>
        </div>
    </header>
  )
}
