import React from 'react'
import landingimg from "./../images/landing.jpg"

export default function Landing() {
    return (
        <div className="landing">
            <img src={landingimg} alt="" className="landimg"/>
            <div className="text">
                <h1>أكاديمية عامر</h1>
                <p>للرياضيات</p>
            </div>
        </div>
    )
}
